@media only screen and (max-width:700px) {
    .editMedicalCard{
        background-image: url("../../img/2.jpeg");
        background-size: 103%;
        height: 100vh;
    }

    .editMedicalCard h1 {
        position: absolute;
        width: 256px;
        height: 39px;
        left: 87px;
        top: 135px;
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 600;
        font-size: 26px;
        line-height: 39px;    
        display: flex;
        align-items: center;
        text-align: center;
        color: #005671;
        backdrop-filter: blur(2px);    
    }

    .editMedicalCard a {
        position: absolute;
        font-family: 'Poppins'; 
        left: 25px;  
        font-size: 18px;
    }


    .editMedicalCard .inp {
        box-sizing: border-box;
        position: absolute;
        width: 212px;
        height: 42px;
        left: 25px;
        background: rgba(160, 164, 165, 0.29);
        border: 1px solid #000000;
    }

    .editMedicalCard .firstNameh3{
        top: 200px;
    }
    .editMedicalCard .firstName {
        top: 225px;
    }

    .editMedicalCard .lastNameh3{
        top: 278px;
    }

    .editMedicalCard .lastName {
        top: 302px;
    }

    .editMedicalCard .phoneNumberUpdateh3 {
        top: 355px;
    }

    .editMedicalCard .phoneNumberUpdate {
        top: 379px;
    }

    .editMedicalCard .cityUpdateh3 {
        top: 428px;
    }

    .editMedicalCard .cityUpdate {
        top: 453px;
    }

    .editMedicalCard .streetAddressUpdateh3 {
        top: 506px;
    }

    .editMedicalCard .streetAddressUpdate {
        top: 530px;
    }

    .editMedicalCard .emergencyContactPersonh3{
        top: 579px;
    }

    .editMedicalCard .emergencyContactPerson{
        box-sizing: border-box;
        position: absolute;
        height: 42px;
        left: 25px;
        background: rgba(160, 164, 165, 0.29);
        border: 1px solid #000000;
        top: 605px;
    }

    .editMedicalCard .emergencyContactPersonYes {
        width: 212px;
    }

    .editMedicalCard .emergencyContactPersonNo {
        width: 350px;
    }

    .editMedicalCard .regularMedications {
        position: absolute;
        left: 25px;
        top: 679px;
        font-size: 18px;
        font-family: 'Poppins';
    }

    .editMedicalCard .btnRegularMedications{
        border: none;
        opacity: 67%;
        color: white;
        font-size: 19px;
        font-weight: bold;
        transition: all 0.5s;
        cursor: pointer;
        position: absolute;
        width: 50px;
        height: 30px;
        background: #7EC8DF;    
    }

    .editMedicalCard .btnRegularMedicationsYes {
        left: 25px;
        top: 715px;
    }

    .editMedicalCard .btnRegularMedicationsNo {
        left: 90px;
        top: 715px;
    }

    .editMedicalCard .regularMedicationsText {
        position: absolute;
        left: 32px;
        top: 770px;
        width: 330px;
        height: 44px;
    }

    .editMedicalCard .btnUpdate{
        border: none;
        opacity: 67%;
        color: white;
        font-size: 24px;
        font-weight: bold;
        transition: all 0.5s;
        cursor: pointer;
        position: absolute;
        width: 220px;
        height: 35px;
        left: 85px;
        background: #7EC8DF;
    }

    .editMedicalCard .btnUpdateNo{
        top: 780px;
    }

    .editMedicalCard .btnUpdateYes{
        top: 830px;
    }

    ::placeholder {
        color: white;
    }


    .editMedicalCard .gohome{
        position: absolute;
        width: 47px;
        height: 24px;
        left: 320px;
        top: 20px;
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 24px;    
        display: flex;
        align-items: center;
        color: #58929C;
    }
}