.patientQuestionnaire .msgContainer .h1Questionnaire,
.patientQuestionnaire .msgContainer .h1Questionnaire1 {
  padding: 0;
  margin: 0;
}

.patientQuestionnaire {
  background-image: url("../../img/10.jpg");
  background-size: 100%;
  height: 97vh;
}

.patientQuestionnaire .aboveTheContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.patientQuestionnaire .container {
  border: 4px solid white;
  background-color: rgba(255, 255, 255, 0.881);
  width: 556px;
  height: 547px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5%;
}

.patientQuestionnaire .questionnaire {
  border-collapse: collapse;
  direction: rtl;
}

.patientQuestionnaire .questionnaire1 {
  border-collapse: collapse;
  direction: ltr;
}

.patientQuestionnaire .msgContainer {
  display: flex;
  flex-direction: column;
  direction: rtl;
  align-self: flex-end;
}

.patientQuestionnaire .msgContainer1 {
  display: flex;
  flex-direction: column;
  direction: ltr;
  align-self: flex-start;
}

.patientQuestionnaire .msgContainer .h1Questionnaire {
  font-family: "Poppins";
  font-style: normal;
  font-size: 26px;
  line-height: 39px;
  color: #005671;
}

.patientQuestionnaire .msgContainer1 .h1Questionnaire1 {
  font-family: "Poppins";
  font-style: normal;
  font-size: 26px;
  line-height: 39px;
  color: #005671;
}

.patientQuestionnaire .questionnaire th,
td {
  padding: 3px;
  text-align: center;
  border-bottom: 1px solid #ddd;
  font-size: 15px;
}

.patientQuestionnaire .questionnaire th {
  background-color: #add8e6;
  color: #fff;
  font-size: 15px;
}

.patientQuestionnaire .questionnaire tr:nth-child(even) {
  background-color: #f2f2f2;
}

.patientQuestionnaire .questionnaire1 th,
td {
  padding: 3px;
  text-align: center;
  border-bottom: 1px solid #ddd;
  font-size: 15px;
}

.patientQuestionnaire .questionnaire1 th {
  background-color: #add8e6;
  color: #fff;
  font-size: 15px;
}

.patientQuestionnaire .questionnaire1 tr:nth-child(even) {
  background-color: #f2f2f2;
}

.patientQuestionnaire .btnOk {
  border: none;
  opacity: 67%;
  color: white;
  font-size: 24px;
  font-weight: bold;
  transition: all 0.5s;
  cursor: pointer;
  width: 218px;
  height: 41px;
  background: #7ec8df;
}

.patientQuestionnaire .BtnContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 15px;
}

.patientQuestionnaire .gohomeQuestionnaire {
  cursor: pointer;
  font-family: "Poppins";
  font-style: normal;
  color: #58929c;
  display: flex;
  justify-content: right;
  margin-right: 16px;
  margin-bottom: 16px;
  padding-right: 14px;
}

