.registration-page{
  background-image: url("../../img/10.jpg");
  background-size: 100%;
  height: 97vh;
}

.registration-page .aboveTheContainer{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.registration-page .container{
  border: 4px solid white;
  background-color: rgba(255, 255, 255, 0.881);
  width: 298px;
  height: 391px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5%;
  gap: 31px;
}

.registration-page .btn{
  width: 260px;
  height: 54px;
  background-color: #7ec8df;
  border: none;
  opacity: 67%;
  color: white;
  font-size: 24px;
  font-weight: bold;
  transition: all 0.5s;
  cursor: pointer;
}

.registration-page .image{
  width: 217px;
  height: 180px;
}

.registration-page .gohome{
  cursor: pointer;
  font-family: 'Poppins';
  font-style: normal;
  color: #58929C;
  display: flex;
  justify-content: right;
  margin-right: 16px;
  margin-bottom: 103px;
}

  @media only screen and (max-width:1250px) {
    .registration-page {
      background-image: url("../../img/12.png");
    }
  }
  
  @media only screen and (max-width:701px) {
    .registration-page {
      background-image: url("../../img/11.jpg");
    }

    .registration-page .btnPatient{
      margin-bottom: 12px;
    }
  }

  @media only screen and (max-width:350px) {
    .registration-page{
      height: 112vh;
    }

    .registration-page .container{
      width: 241px;
      height: 354px;
    }
    
    .registration-page .btn{
      width: 198px;
      height: 44px;
    }

    .registration-page .btnPatient{
      margin-bottom: -13px;
    }
  }