@media only screen and (max-width:700px) {
    body{
        background-color: #eefdfc;
    }

    .UserPage .gohome{
        position: absolute;
        width: 47px;
        height: 24px;
        left: 320px;
        top: 20px;
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 24px;    
        display: flex;
        align-items: center;
        color: #58929C;
    }

    .UserPage{
        background-image: url("../../img/2.jpeg");
        background-size: cover;
        height: 100vh;
        width: 100vw;
        padding: 1.5rem;
    }

    .user-list{
        display: flex;
        align-items: center;
        flex-direction: column;
    }

    .checksome{
        display: flex;
        flex-direction: column;
        margin-bottom: 50px;
    }

    .UserPage .Therapist{
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 600;
        font-size: 20px;
        align-items: center;
        text-align: center;
        color: #005671;
        margin-top: 140px;
    }

    .UserPage-h1{
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 600;
        font-size: 26px;
        text-align: center;
        color: #005671;
        margin-top: 110px;
    }

    .first_last{
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 300;
        font-size: 16px;
        line-height: 24px;
        align-items: center;
        color: #000000;
    }

    .email{
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 300;
        font-size: 16px;
        line-height: 24px;
        align-items: center;
        color: #000000;
    }

    .phon{
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 300;
        font-size: 16px;
        line-height: 24px;
        align-items: center;
        color: #000000;
    }

    .city{
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 300;
        font-size: 16px;
        line-height: 24px;
        align-items: center;
        color: #000000;
    }

    .userType{
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 300;
        font-size: 16px;
        line-height: 24px;
        align-items: center;
        color: #000000;
    }

    .confirmation{
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 300;
        font-size: 16px;
        line-height: 24px;
        align-items: center;
        color: #000000;
    }

    .showDiploma{
        border: none;
        opacity: 67%;
        color: white;
        font-size: 14px;
        font-weight: bold;
        transition: all 0.5s;
        cursor: pointer;
        background: #7EC8DF;
    }

    .ok{
        border: none;
        opacity: 67%;
        color: white;
        font-size: 14px;
        font-weight: bold;
        transition: all 0.5s;
        cursor: pointer;
        background: #7EC8DF;
        margin-top: 5px;
    }

    .delete_confirmation{
        border: none;
        opacity: 67%;
        color: white;
        font-size: 14px;
        font-weight: bold;
        transition: all 0.5s;
        cursor: pointer;
        background: #7EC8DF;
        margin-top: 5px;
    }
}