.breathe h1,
.hands h1,
.neck h1,
.sea h1{
  padding: 0;
  margin: 0;
}

.breathe {
  background-image: url("../../img/10.jpg");
  background-size: 100%;
  height: 97vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.breathe .container{
    border: 4px solid white;
    background-color: rgba(255, 255, 255, 0.881);
    width: 331px;
    height: 539px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 5%;
}

.breathe h1{
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 600;
    font-size: 36px;
    line-height: 39px;
    display: flex;
    align-items: center;
    text-align: center;
    color: #005671;
    margin-top: -78px;
}

.breathe .lotusFlower{
    width: 200px;
    height: 200px;
    margin: 20px 0;
}

.breathe .breatheVideo{
    width: 322px;
    margin: 40px 0;
}

